.main-hero-con{
margin: 20px;
}
.res-button{
    width: 150px;
    height: 60;
    padding:20px 10px;
    margin: 50px 0;
    text-align: center;
    border-radius: 6px;
    box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;
    background: linear-gradient(145deg, #e2e8ec, #ffffff);
    /* align-self: flex-start; */
}
.res-button:hover{
    color:#FF014F;
    transition: color .2s ease-in ;
}
.hero-des{
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

.main-hero-con{
    grid-template-columns: repeat(2,1fr);
    margin: auto;
    height: auto;
}

.hero-img{
    /* ,url(../images/h102.gif) */
  /* background: linear-gradient(145deg,  #ffffff00,#e2e8ec21); */
  /* box-shadow: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff; */
  border-radius: 20px;
}
/* .hero-img>img{
    visibility: hidden;
} */
@media only screen and (max-width:560px) {
    .hero-img{
        display: none;
    }
    .main-hero-con{
        grid-template-columns: repeat(1,1fr);
        margin: auto;
        height: auto;
        /* font-size: 48px; */
    }
}
